<template>
  <div>
    <el-dialog :visible.sync="newCompanyDialog">
      <div slot="title" style="color: white; text-align: center">
        New Company
      </div>
      <div class="input-group">
        <span class="label">Company Name</span>
        <el-input v-model="company.name"></el-input>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickCreate">Create</el-button>
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    newCompanyDialog: {
      get() {
        return this.$store.state.newCompanyDialogVisibility;
      },
      set(v) {
        this.$store.commit("setNewCompanyDialogVisibility", v);
      },
    },
  },
  data() {
    return {
      company: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    onClickCreate() {
      this.$store.dispatch("company/create", {
        company: this.company,
        notify: this.$notify,
      });
      this.initCompanyObject();
      this.newCompanyDialog = false;
    },
    onClickCancel() {
      this.initCompanyObject();
      this.newCompanyDialog = false;
    },
    initCompanyObject() {
      this.company = {
        name: "",
      };
    },
  },
  created() {
    this.initCompanyObject();
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
