<template>
  <div>
    <el-dialog :visible.sync="editCompanyDialog" v-if="company">
      <div slot="title" style="color: white; text-align: center">
        Edit Company
      </div>
      <div class="input-group">
        <span class="label">Company Name</span>
        <el-input v-model="company.name"></el-input>
      </div>

      <div class="action-buttons">
        <el-button class="btn-create" @click="onClickUpdate">Update</el-button>
        <el-button class="btn-delete" @click="openDeleteCompanyDialog"
          >Delete</el-button
        >
        <el-button class="btn-cancel" @click="onClickCancel">Cancel</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    editCompanyDialog: {
      get() {
        return this.$store.state.editCompanyDialogVisibility;
      },
      set(v) {
        this.$store.commit("setEditCompanyDialogVisibility", v);
      },
    },
    ...mapState("company", ["updateCompany"]),
  },
  data() {
    return {
      company: null,
      options: [
        {
          value: true,
          label: "Active",
        },
        {
          value: false,
          label: "Deactivate",
        },
      ],
    };
  },
  methods: {
    openDeleteCompanyDialog() {
      this.editCompanyDialog = false;
      this.$store.commit("setDeleteCompanyDialogVisibility", true);
    },
    onClickUpdate() {
      this.$store.dispatch("company/update", {
        company: this.company,
        notify: this.$notify,
      });
      this.editCompanyDialog = false;
    },
    onClickCancel() {
      this.editCompanyDialog = false;
    },
  },
  watch: {
    editCompanyDialog() {
      this.company = { ...this.updateCompany };
    },
  },
};
</script>

<style lang="scss" scoped>
@import "src/assets/scss/new-user-modal.scss";
</style>
